import './ReportsPage.styl';

import React, {
  Component,
} from 'react';

import PropType from 'prop-types';

import {
  Header,
  HeaderBottom,
  ReportsList,
  Report
} from 'components';
import FlatButton from 'material-ui/FlatButton';

import { bindActionCreators }       from 'redux';
import { connect }                  from 'react-redux';

import {
  ReportActions
} from 'actions';

const btnStyle = {
  style: {
    marginTop: '40px',
    color: '#fff',
    borderRadius: '4px',
    width: '110px',
    height: '40px'
  },
  labelStyle: {
    fontSize: '16px',
    fontWeight: '600',
    letterSpacing: '1px'
  }
};

@connect(state => ({
  reportData: state.reportReducer
}), dispatch => ({
  reportActions: bindActionCreators(ReportActions, dispatch)
}))

export default class ReportsPage extends Component {
  static propTypes = {
    match: PropType.object,
    reportData: PropType.object,
    reportActions: PropType.object
  };

  render() {
    const path = this.props.match.path;

    const {
      reportData
    } = this.props.reportData;

    const {
      reportActions
    } = this.props;

    return (
      <div className="c-reports-page">
        <div className="reports-page-container">
          <Header
            indexPage={2}
          />
          <HeaderBottom
            bigTitle="Reports"
            oneBlock={true}
          />
          <div className="body-reports-wrapper">
            {
              path === '/reports' &&
                <ReportsList
                  data={reportData}
                  reportActions={reportActions}
                />
            }
            {
              path === '/new-report' &&
                <div>
                  <Report />
                  <div className="btn-done">
                    <FlatButton
                      label="Done"
                      backgroundColor="#f07171"
                      style={btnStyle.style}
                      labelStyle={btnStyle.labelStyle}
                    />
                  </div>
                </div>
            }
          </div>
        </div>
      </div>
    );
  }
}



